.create-new-accaunt {
  width: 384px;
  height: auto;
  min-height: 600px;
  margin: 60px auto;
  padding: 32px 48px;
  background-color: #ffffff;
  border: 1px solid #D9D9D9;
  box-shadow: 0px 22px 106px rgba(0, 0, 0, 0.07),
    0px 9px 44px rgba(0, 0, 0, 0.05),
    0px 5px 24px rgba(0, 0, 0, 0.04),
    0px 3px 13px rgba(0, 0, 0, 0.035),
    0px 2px 7px rgba(0, 0, 0, 0.028),
    0px 1px 3px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
}

.sign-in-accaunt {
  width: 384px;
  height: auto;
  min-height: 374spx;
  margin: 60px auto;
  padding: 32px 48px;
  background-color: #ffffff;
  border: 1px solid #D9D9D9;
  box-shadow: 0px 22px 106px rgba(0, 0, 0, 0.07),
    0px 9px 44px rgba(0, 0, 0, 0.05),
    0px 5px 24px rgba(0, 0, 0, 0.04),
    0px 3px 13px rgba(0, 0, 0, 0.035),
    0px 2px 7px rgba(0, 0, 0, 0.028),
    0px 1px 3px rgba(0, 0, 0, 0.02);
  border-radius: 6px;
}

.form-create {
  display: flex;
  flex-direction: column;
  align-items:stretch;  
}

.form-create h3 {
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  /* line-height: 28px; */
  text-align: center;
  color: #262626;
}

.label {
  margin-top: 12px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #262626;
}

.input {
  width: 100%;
  height: 40px;
  background-color: #ffffff;
  border: 1px solid #d9d9d9;
  border-radius: 4px;
  padding: 8px 12px;

  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #292929;
}

.input::placeholder {
  color: #BFBFBF;
}

.hr {
  border-top: 1px solid #e8e8e8;
  margin-top: 20px;
}

.error-massage {
  display: block;
  padding: 0;
  margin: 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  /* letter-spacing: -0.02em; */
  line-height: 22px;
  color: #F5222D;
}

.error-border {
  border: 1px solid #F5222D;
}

.rules-block {
  margin-top: 8px;
}


input[type="checkbox"] {
  display: none;
}



.rules {
  /* padding-left: 20px; */
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: #595959;
}


.rules::before {
  content: " ";
  color: #4a4a4a;
  display: inline-block;
  font: 20px/30px Arial;
  margin-right: 8px;
    
  position: relative;
  /* text-align: center; */
  /* text-indent: 9px; */
  width: 16px;
  height: 16px;
  background: #ffffff;
  border: 1px solid #9ABBCE;
  border-radius: 2px;
  border-image: initial;
  vertical-align: middle;
}

input[type="checkbox"]:checked + .rules::before {
  content: " ";
  border: 1px solid #1890ff;
  background-color: #1890ff;
  background-image: url('../../images/check.svg');
  background-repeat: no-repeat;
  background-position: center center;
}




.btn-primary {
  background: #1890FF;
  border: none;
  border-radius: 4px;
  height: 40px;
  margin-top: 21px;

  font-family: Roboto;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;
}

.footnote {
  margin-top: 8px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  color: #8C8C8C;
}

.footnote a {
  text-decoration: none;
  color: #1890FF;
}

